	module.exports = {"data":{"markdownRemark":{"html":"<p>Back in the September this year, I visited for the first time San Francisco. It was my first visit to the USA and first transatlantic flight.</p>\n<p>Oh, man! It was great. </p>\n<p><strong>Route:</strong> Berlin - München, München - SFO</p>\n<p><strong>Travel time:</strong> 14+ hours</p>\n<p><strong>Conference ticket price</strong>: $250</p>\n<p>I met so many great people during my trip and stayed for a week in the downtown SF.</p>\n<h2 id=\"day-one---guided-tour\"><a href=\"#day-one---guided-tour\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Day one - guided tour</h2>\n<p>We met with the guys before the conference for the Friday Fun Day activities. The meeting point was in the Ferry Building Marketplace. We grabbed some Blue Bottle Coffee (I got the trend, huh!). It was a great opportunity to get to know each other before the conference, meet organizers and enjoy the guided tour (including cable cars!). The tour was led by the conference organizer <a href=\"https://twitter.com/Benghamine\">Benjamin Dunphy</a>. Thanks, Ben for the great organization and showing us spectacular views! :)</p>\n<img src=\"https://user-images.githubusercontent.com/9251327/48370119-ab78ed00-e6b8-11e8-95ae-ccd4edf5c951.jpg\" width=\"700\" height=\"700\">\n<p>View on downtown</p>\n<img src=\"https://user-images.githubusercontent.com/9251327/48369288-6a7fd900-e6b6-11e8-84bc-b96519a677df.jpg\" width=\"700\" height=\"700\">\n<p>Russian Hill</p>\n<img src=\"https://user-images.githubusercontent.com/9251327/48370253-0dd1ed80-e6b9-11e8-9815-9f4b92d6685c.jpg\" width=\"700\" height=\"700\">\n<p>View on the Alcatraz</p>\n<img src=\"https://user-images.githubusercontent.com/9251327/48370322-48d42100-e6b9-11e8-9fc7-32313276ec43.jpg\" width=\"700\" height=\"700\">\n<p>Lombard Street</p>\n<img src=\"https://user-images.githubusercontent.com/9251327/48370346-59849700-e6b9-11e8-9b9f-dce010f9db71.jpg\" width=\"700\" height=\"700\">\n<h2 id=\"day-one---lunch-break\"><a href=\"#day-one---lunch-break\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Day one - lunch break</h2>\n<p>After walking around the Russian Hill and enjoying good lunch we separated and went sightseeing. Checked out Golden Gate Bridge, Lombard Street, and surroundings. After chilling in Mission Dolores Park we took a ride to Microsoft Reactor building for the conference opening.</p>\n<p>I feel like Mission Dolores Park is my favorite place in SF along with the Golden Gate Park :)</p>\n<p>Mission Dolores Park</p>\n<img src=\"https://user-images.githubusercontent.com/9251327/48370504-d879cf80-e6b9-11e8-8b5d-efad405996dd.jpg\" width=\"700\" height=\"700\">\n<h2 id=\"day-one---microsoft-reactor---topic-tables\"><a href=\"#day-one---microsoft-reactor---topic-tables\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Day one - Microsoft Reactor - Topic Tables</h2>\n<p>Topic Tables was a very interesting experience for me. You get around the tables with presenters and discuss what is interesting to you. It encourages informal communication with the presenters for talks so that you can ask all the questions and discuss topics you're interested the most. I had a good chat with the guys about GraphQL, functional lenses by James McNamara, accessibility etc.</p>\n<h2 id=\"day-two---main-event---github-hq\"><a href=\"#day-two---main-event---github-hq\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Day two - main event - GitHub HQ</h2>\n<p>We've got a full day of talks from leading experts in the JS/React community. </p>\n<p>I was impressed that all the talks are interesting and very, very good quality. From slides to how people present, it was engaging and inspiring.</p>\n<p>You can find recordings here <a href=\"https://twitter.com/reactathon\">https://twitter.com/reactathon</a></p>\n<h2 id=\"venue\"><a href=\"#venue\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Venue</h2>\n<img src=\"https://user-images.githubusercontent.com/9251327/48509035-5079fe00-e850-11e8-8e81-d64425467e3f.jpg\" width=\"700\" height=\"700\">\n<p>We spend a full day with the talks in the GitHub HQ. I pleased to see GitHub main office and also grab a bunch of presents from Real World React and sponsors. Stickers of course! An environment is so great there. I enjoyed playing ping pong with <a href=\"https://twitter.com/maxmarchuk\">Max</a>. Hey Max! Hope you're doing well!</p>\n<p>Famous Octocat</p>\n<img src=\"https://user-images.githubusercontent.com/9251327/48509088-6b4c7280-e850-11e8-88e0-3de994ede24a.jpg\" width=\"700\" height=\"700\">\n<h2 id=\"thanks\"><a href=\"#thanks\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Thanks</h2>\n<p>I'm thankful to organizers, speakers and all the people I've met and had a chat with. You rock!</p>\n<ul>\n<li><a href=\"https://twitter.com/maxmarchuk\">Max</a></li>\n<li><a href=\"https://twitter.com/swcarlosrj\">Carlos</a></li>\n<li><a href=\"https://twitter.com/upvalueio\">Phil</a></li>\n<li><a href=\"https://twitter.com/Benghamine\">Ben</a></li>\n</ul>\n<p>...and all many others amazing folks!</p>\n<p>Unfortunately, I can't fit all my impressions and stories in one article. I would recommend everyone to visit a conference in another country. It was 100% worth the long flight. I've stayed for one week in the SF and enjoyed my experiences very much despite the shocking (for me) situation in the downtown with the homeless people around.</p>\n<img src=\"https://user-images.githubusercontent.com/9251327/48508961-32ac9900-e850-11e8-81bb-8b3893dd728a.jpg\" width=\"700\" height=\"700\">\n<p>Stay safe! Stay curious!</p>","timeToRead":2,"excerpt":"Back in the September this year, I visited for the first time San Francisco. It was my first visit to the USA and first transatlantic flight…","frontmatter":{"title":"Reactathon 2018 React Conference in San Francisco","cover":"https://images.unsplash.com/photo-1449034446853-66c86144b0ad","date":"2018-11-12","category":"tech","tags":["code","travel","san francisco","usa","conference","react","bay area"],"author":"alex"},"fields":{"slug":"/reactathon-2018-react-conference-in-san-francisco"}},"prev":{"excerpt":"Circular or cyclic dependencies could easily cause bugs in your application. When your modules reference each…","frontmatter":{"title":"How to avoid circular dependencies in your JavaScript monorepo","cover":"https://images.unsplash.com/photo-1478754117940-f5bc84311001?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=750&q=80%20750w","date":"2019-06-26"},"fields":{"slug":"/how-to-avoid-circular-dependencies-in-your-java-script-monorepo"}},"next":{"excerpt":"When working on any project you'll eventually end up having legacy code which needs to be refactored…","frontmatter":{"title":"Golden rules of code refactoring","cover":"https://images.unsplash.com/photo-1508830524289-0adcbe822b40?ixlib=rb-0.3.5&ixid=eyJhcHBfaWQiOjEyMDd9&s=377e356cce015c363d123504857f8137&auto=format&fit=crop&w=2511&q=80","date":"2018-10-01"},"fields":{"slug":"/golden-rules-of-code-refactoring"}},"authors":{"edges":[{"node":{"id":"alex","name":"Alexander Grischuk","image":"https://www.dropbox.com/s/ifvhc39k53udz1e/profile-pic-size.jpg?raw=1","url":"https://www.linkedin.com/in/alxgi/","bio":"Frontend Engineer at Signavio. I like to build apps with React while keeping good UX in mind."}}]}},"pathContext":{"slug":"/reactathon-2018-react-conference-in-san-francisco","total":9,"prev":"/how-to-avoid-circular-dependencies-in-your-java-script-monorepo","next":"/golden-rules-of-code-refactoring"}}

